<template>
  <div
    class="img-button"
    :style="{'background-image': 'url(' + require('../assets/img/' + image + '.jpg') + ')'}"
    @click="setBackground(board, image, oldImg, imgIndex)"
  ></div>
</template>

<script>
export default {
  props: {
    board: {
      type: Object,
      required: true
    },
    image: {
      type: String,
      required: true
    },
    oldImg: {
      type: String,
      required: true
    },
    imgIndex: {
      type: Number,
      required: true
    }
  },
  methods: {
    setBackground(board, newImg, oldImg, imgIndex) {
      this.$store.commit("CHANGE_IMAGE", {
        board,
        newImg,
        oldImg,
        imgIndex
      });
    }
  }
};
</script>

<style scoped>
.img-button {
  background-color: black;
  width: 35px;
  height: 35px;
  float: right;
  margin: 5px;
  background-size: cover;
}
.img-button:hover {
  cursor: pointer;
}
</style>
